<template>
  <li @click="
      $emit('music-player', HotSong);
      $emit('update-song-list', HotSongList);
    ">
    <p class="fl">{{ String(index + 1).padStart(2, "0") }}</p>
    <div class="fr">
      <div class="content">
        <h3>{{ HotSong.name }}</h3>
        <div class="msg">
          <span></span>
          <p v-for="songer in HotSong.ar" :key="songer.id">
            {{ songer.name }}
          </p>
          <p>- {{ HotSong.al.name }}</p>
        </div>
      </div>
      <div class="icon"></div>
    </div>
  </li>
</template>

<script>
export default {
  props: {
    HotSong: Object,
    index: Number,
    HotSongList:Array
  },
  data() {
    return {};
  },
  computed: {},
  methods: {},
};
</script>

<style lang="less" scoped>
li {
  display: flex;

  &:nth-child(1),
  &:nth-child(2),
  &:nth-child(3) {
    a p.fl {
      color: #df3436;
    }
  }

  p.fl {
    width: 40rem;
    height: 55rem;
    font-size: 17rem;
    text-align: center;
    line-height: 55rem;
    color: #999999;
  }

  div.fr {
    width: calc(100vw - 40rem);
    border-bottom: 1rem solid #e8e8e9;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .content {
      h3 {
        font-size: 17rem;
      }
      .msg {
        display: flex;
        align-items: center;
        span {
          display: block;
          background: url("../assets/index_icon_2x.png") no-repeat 0 0;
          width: 12px;
          height: 8px;
          background-size: 165rem;
          margin-right: 5rem;
        }
        p {
          color: #aa9991;
        }
      }
    }
    .icon {
      height: 22px;
      width: 22px;
      background: url("../assets/index_icon_2x.png") no-repeat -23px 0;
      background-size: 160rem;
      margin-right: 10rem;
    }
