<template>
  <div class="hot">
    <div class="hotop">
      <div class="icon"></div>
      <div class="updata-time">更新日期： {{ time }}</div>
    </div>
    <ul>
      <HotList
        v-for="(HotSong, index) in HotSongList"
        :key="HotSong.id"
        :HotSong="HotSong"
        :index="index"
        :HotSongList="HotSongList"
        @music-player="$emit('music-player', $event)"
        @update-song-list="$emit('update-song-list', $event)"
      ></HotList>
    </ul>
    <div class="loading" v-if="loading">
      <img src="../assets/下载.gif" alt="" />
    </div>
    <footer>
      <a href="">查看完整榜单 ></a>
    </footer>
  </div>
</template>
<script>
import HotList from "@/components/HotList.vue";

export default {
  components: {
    HotList,
  },
  data() {
    return {
      HotSongList: [],
      time: "",
      loading: false,
    };
  },

  methods: {
    getTime: function () {
      let nowTime = new Date();
      let month = nowTime.getMonth() + 1;
      let day = nowTime.getDate();
      this.time = `${month}月${day}日`;
      return `${month}月${day}日`;
    },
  },
  mounted() {
    this.getTime();
  },
  created() {
    this.loading = true;
    this.axios("https://apis.netstart.cn/music/playlist/track/all", {
      params: {
        id: 3778678,
        limit: 20,
      },
    }).then((n) => {
      this.loading = false;
      this.HotSongList = n.data.songs;
    });
  },
};
</script>

