<template>
  <div class="input-search">
    <i class="fa fa-search" aria-hidden="true"></i>
    <input
      type="text"
      placeholder="搜索歌曲、歌手、专辑"
      v-model="value"
      @keyup.enter="addHistory"
      @keyup="$emit('input-value', value)"
      @focus="$emit('focus-input')"
      ref="input"
    />
    <i
      v-if="isShow"
      class="fa fa-times-circle"
      aria-hidden="true"
      @click="deleteValue"
    ></i>
  </div>
</template>

<script>
export default {
  data() {
    return {
      value: "",
    };
  },
  props: {
    isShow: Number,
    inputvalue: String,
  },
  methods: {
    addHistory: function () {
      this.value && this.$emit("add_todo", this.value);
      // this.value = "";
      this.$refs.input.blur();
    },
    deleteValue: function () {
      this.$emit("delete-value");
      this.value = "";
    },
  },
  watch: {
    inputvalue: function () {
      this.value = this.inputvalue;
    },
  },
};
</script>

<style lang="less" scoped>
.input-search {
  width: 95%;
  height: 30rem;
  background: #ebecec;
  border-radius: 15rem;
  margin: 15rem 10rem;
  color: #c9c9ca;
  display: flex;
  align-items: center;
  i {
    display: block;
    font-size: 14rem;
    margin: 0 8rem;
  }
  input {
    width: calc(100% - 60rem);
    height: 30rem;
    line-height: 30rem;
    border-radius: 15rem;
    background: #ebecec;
    outline: none;
    border: 0;
  }
}
</style>