<template>
  <li @click="$emit('search-history',h)">
    <i class="fa fa-clock-o clock" aria-hidden="true"></i>
    <div>
      <p>{{ h }}</p>
      <i
        class="fa fa-times time"
        aria-hidden="true"
        @click.stop="deleteHistory"
      ></i>
    </div>
  </li>
</template>

<script>
export default {
  props: {
    h: String,
  },
  methods: {
    deleteHistory:function(){
      this.$emit("delete-History",this.h)
    }
  },
};
</script>

<style lang="less" scoped>
li {
  height: 45rem;
  display: flex;
  align-items: center;
  i.clock {
    font-size: 20rem;
    color: #cfd0d0;
    margin: 0 10rem;
  }
  div {
    width: calc(100vw - 40rem);
    height: 45rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1.5rem solid #cfd0d0;
    p {
      font-size: 16rem;
    }
    i.time {
      font-size: 16rem;
      color: #999899;
      margin-right: 10rem;
    }
  }
}
</style>