<template>
  <li
    @click="
      $emit('music-player', song);
      $emit('update-song-list', playlistRes);
    "
  >
    <p class="fl">{{ index + 1 }}</p>
    <div class="fr">
      <div class="content">
        <h3>{{ song.name }}</h3>
        <div class="msg">
          <p v-for="songer in song.ar" :key="songer.id">{{ songer.name }}</p>
          <p>-{{ song.al.name }}</p>
        </div>
      </div>
      <div class="icon"></div>
    </div>
  </li>
</template>

<script>
export default {
  props: {
    song: Object,
    index: Number,
    playlistRes: Array,
  },
};
</script>

