<template>
  <div class="playlist" v-if="playlistHeader">
    <div class="header">
      <div
        class="bg"
        :style="`background:url(${playlistHeader?.coverImgUrl})`"
      ></div>
      <div class="content">
        <div class="img">
          <img :src="`${playlistHeader?.coverImgUrl}`" alt="" />
          <p>精品歌单</p>
          <span class="num">
            <img src="../assets/下载.svg" alt="" />
            {{ Math.round(playlistHeader.playCount / 1000) / 10 }}万
          </span>
        </div>
        <div class="msg">
          <p>{{ playlistHeader.name }}</p>
          <div class="creator">
            <img :src="playlistHeader?.creator?.avatarUrl" alt="" />
            <p>{{ playlistHeader?.creator?.nickname }}</p>
          </div>
        </div>
      </div>
      <div class="svg" @click="$router.back()">
        <svg
          t="1664367914056"
          class="icon"
          viewBox="0 0 1024 1024"
          version="1.1"
          xmlns="http://www.w3.org/2000/svg"
          p-id="4709"
          width="20"
          height="20"
        >
          <path
            fill="#ffffff"
            d="M532.526499 904.817574L139.506311 511.797385 532.526499 118.777197c12.258185-12.258185 12.432147-32.892131-0.187265-45.51052-12.707416-12.707416-32.995485-12.703323-45.511543-0.187265L75.166957 484.739123c-7.120165 7.120165-10.163477 17.065677-8.990768 26.624381-1.500167 9.755178 1.5104 20.010753 8.990768 27.491121l411.660734 411.660734c12.258185 12.258185 32.892131 12.432147 45.511543-0.187265 12.707416-12.707416 12.7023-32.995485 0.187265-45.51052z"
            p-id="4710"
          ></path>
        </svg>
      </div>
    </div>
    <div class="list-title">
      <p>歌曲列表</p>
    </div>
    <ul class="songlist">
      <PlayList
        v-for="(song, index) in playlistRes"
        :key="song.id"
        :song="song"
        :index="index"
        :playlistRes="playlistRes"
        @music-player="$emit('music-player', $event)"
        @update-song-list="$emit('update-song-list', $event)"
      ></PlayList>
    </ul>
    <div class="more">
      <a href="">查看更多歌曲，请下载客户端</a>
    </div>

    <div class="comment">
      <p>精彩评论</p>
      <ul>
        <PlayComment
          v-for="com in comments"
          :key="com.commentId"
          :com="com"
        ></PlayComment>
      </ul>
      <div class="moreComment">
        <p>查看全部{{ total }}条评论 ></p>
      </div>
    </div>

    <!-- <div class="footer">
      <p>收藏歌单</p>
    </div> -->

    <div class="loading" v-show="loading">
      <img src="../assets/下载.gif" alt="" />
    </div>
  </div>
</template>

<script>
import PlayList from "@/components/PlayList.vue";
import PlayComment from "@/components/PlayComment.vue";

export default {
  components: {
    PlayList,
    PlayComment,
  },
  data() {
    return {
      playlistRes: [],
      playlistHeader: null,
      loading: false,
      comments: [],
      total: "",
    };
  },
  props: {},
  created() {
    this.loading = true;
    Promise.all([
      this.axios("https://apis.netstart.cn/music/playlist/track/all", {
        params: {
          id: this.$route.query.id,
          limit: 10,
          offset: 0,
        },
      }),
      this.axios("https://apis.netstart.cn/music/playlist/detail", {
        params: {
          id: this.$route.query.id,
        },
      }),
    ]).then(([a, d]) => {
      this.loading = false;
      this.playlistRes = a.data.songs;
      this.playlistHeader = d.data.playlist;
    });

    this.axios("https://apis.netstart.cn/music/comment/playlist", {
      params: {
        id: this.$route.query.id,
      },
    }).then((res) => {
      this.total = res.data.total;
      this.comments = res.data.hotComments;
    });
  },
  methods: {},
};
</script>

<style lang="less" scoped>
.playlist {
  position: relative;

  
  .header {
    width: 100vw;
    height: 186rem;

    position: relative;
    overflow: hidden;

    .svg {
      .icon {
        position: absolute;
        top: 10rem;
        left: 10rem;
        z-index: 9;
      }
    }

    .bg {
      width: 100vw;
      height: 186rem;
      position: absolute;
      top: 0;
      left: 0;

      background-repeat: no-repeat;
      background-size: cover;
      background-position: 50%;
      filter: blur(20rem);
      transform: scale(1.5);
    }
    .bg::after {
      content: " ";
      background-color: rgba(0, 0, 0, 0.322);
      position: absolute;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      z-index: 1;
      height: 100%;
      width: 100%;
    }
    .content {
      width: 350rem;
      height: 126rem;
      display: flex;
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      margin: auto;
      .img {
        width: 126rem;
        position: relative;
        img {
          width: 126rem;
        }
        p {
          position: absolute;
          z-index: 3;
          top: 10rem;
          left: 0;
          padding: 0 8px;
          height: 17rem;
          color: #fff;
          font-size: 9rem;
          text-align: center;
          line-height: 17rem;
          background-color: rgba(217, 48, 48, 0.8);
          border-top-right-radius: 17rem;
          border-bottom-right-radius: 17rem;
        }
        span {
          position: absolute;
          right: 2rem;
          top: 0;
          z-index: 3;
          padding-left: 15rem;
          color: #fff;
          font-size: 12rem;
          background-position: 0;
          background-repeat: no-repeat;
          background-size: 11rem 10rem;
          text-shadow: 1rem 0 0 rgb(0 0 0 / 15%);
          img {
            width: 12rem;
            vertical-align: bottom;
          }
        }
      }

      .img::after {
        content: " ";
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 18px;
        z-index: 2;
        background-image: linear-gradient(
          90deg,
          rgba(0, 0, 0, 0),
          rgba(0, 0, 0, 0.2)
        );
      }
      .msg {
        width: calc(350rem - 126rem);
        display: flex;
        flex-shrink: 0;
        flex-direction: column;
        align-items: center;
        margin-left: 10rem;
        p {
          width: 210rem;
          font-size: 17rem;
          color: white;
        }
        .creator {
          width: 100%;
          display: flex;
          align-items: center;
          margin-top: 10rem;

          img {
            width: 35rem;
            height: 35rem;
            border-radius: 50%;
          }
          p {
            font-size: 14rem;
            color: #c7cdd4;
          }
        }
      }
    }
  }
  .list-title {
    width: 100vw;
    height: 23rem;
    background: #eeeff0;
    padding: 0 10rem;
    line-height: 23rem;
    p {
      font-size: 12rem;
      color: #666666;
    }
  }

  ul.songlist {
    margin-top: 15rem;
  }
  .more {
    width: 100vw;
    height: 21rem;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 5rem;
    a {
      text-decoration: none;
      font-size: 14rem;
      color: #999999;
    }
  }

  .comment {
    p {
      margin: 0;
      padding: 4px 10px;
      color: #666;
      font-size: 12px;
      font-weight: 400;
      background: rgba(0, 0, 0, 0.05);
    }
    div.moreComment {
      display: flex;
      justify-content: center;
      p {
        font-size: 14rem;
        padding: 18rem 0;
        color: #999999;
        background: white;
      }
    }
  }

  .footer {
    width: 100vw;
    display: flex;
    justify-content: center;
    align-content: center;
    background: white;
    padding: 11rem 0;
    position: fixed;
    bottom: 0;
    left: 0;
    p {
      width: 315rem;
      height: 40rem;
      background: #ff3a3a;
      color: white;
      border-radius: 20rem;
      font-size: 18rem;
      text-align: center;
      line-height: 40rem;
    }
  }

  div.loading {
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10;
    background: white;
    img {
      width: 40rem;
      height: 40rem;
    }
  }
}
</style>