<template>
  <div class="search">
    <SearchInput
      @add_todo="addHistory"
      @input-value="inputvalue = $event"
      @delete-value="deleteValue"
      :isShow="inputvalue.length"
      :inputvalue="inputvalue"
      @focus-input="(searchResult = []), (searchMultimatch = null)"
    ></SearchInput>
    <p v-show="!searchSuggests.length">热门搜索</p>

    <ul v-if="!searchSuggests.length" class="codeList">
      <SearchCode
        v-for="code in CodeList"
        :key="code.id"
        :code="code"
        @search-Code="searchCode"
      ></SearchCode>
    </ul>

    <SearchTodo
      v-if="!searchSuggests.length"
      :history="history"
      @delete-History="deleteHistory"
      @search-history="searchHistory"
    ></SearchTodo>

    <ul
      v-show="searchSuggests.length && !searchResult.length"
      class="searchSuggests"
    >
      <p>搜索“{{ inputvalue }}”</p>
      <SearchList
        v-for="s in searchSuggests"
        :key="s.keyword"
        :s="s"
        @search-suggest="searchSuggest"
      ></SearchList>
    </ul>

    <div v-if="searchResult.length" class="Multimatch">
      <div class="artist" v-if="searchMultimatch?.artist?.length">
        <div class="img">
          <img
            :src="`${searchMultimatch?.artist[0]?.picUrl}?imageView=1&type=webp&thumbnail=101x0`"
            alt=""
          />
        </div>
        <div class="content">
          <div class="msg">
            <p>
              {{ searchMultimatch?.artist[0]?.occupation }}:
              <span :class="{ active: !searchMultimatch.artist[0].alias[0] }">{{
                searchMultimatch?.artist[0]?.name
              }}</span>
              <span v-if="searchMultimatch.artist[0].alias[0]"
                >({{ searchMultimatch?.artist[0]?.alias[0] }})</span
              >
            </p>
          </div>
          <div class="icon">
            <i class="fa fa-chevron-right" aria-hidden="true"></i>
          </div>
        </div>
      </div>
      <div class="album" v-if="searchMultimatch?.album?.length">
        <div class="img">
          <img
            :src="`${searchMultimatch?.album[0]?.picUrl}?imageView=1&type=webp&thumbnail=101x0`"
            alt=""
          />
        </div>
        <div class="content">
          <div class="msg">
            <p>
              <span
                >{{ searchMultimatch?.album[0]?.type }} ：{{
                  searchMultimatch?.album[0]?.name
                }}</span
              >
              <span>{{ searchMultimatch?.album[0]?.artist.name }}</span>
            </p>
          </div>
          <div class="icon">
            <i class="fa fa-chevron-right" aria-hidden="true"></i>
          </div>
        </div>
      </div>

      <div
        class="playlist"
        v-if="searchMultimatch?.playlist?.length"
        @click="
          $router.push({
            path: '/playlist',
            query: { id: searchMultimatch.playlist[0].id },
          })
        "
      >
        <div class="img">
          <img
            :src="`${searchMultimatch?.playlist[0]?.coverImgUrl}?imageView=1&type=webp&thumbnail=101x0`"
            alt=""
          />
        </div>
        <div class="content">
          <div class="msg">
            <p>
              <span>
                歌单:
                {{ searchMultimatch?.playlist[0]?.name }}
              </span>
              <span
                >by {{ searchMultimatch?.playlist[0]?.creator.nickname }}</span
              >
            </p>
          </div>
          <div class="icon">
            <i class="fa fa-chevron-right" aria-hidden="true"></i>
          </div>
        </div>
      </div>
    </div>

    <ul v-if="searchResult.length" class="searchRes">
      <SearchRes
        v-for="sr in searchResult"
        :key="sr.id"
        :sr="sr"
        :searchResult="searchResult"
        @music-player="$emit('music-player', $event)"
        @update-song-list="$emit('update-song-list', $event)"
      ></SearchRes>
    </ul>
    <div class="loading" v-if="loading">
      <img src="../assets/下载.gif" alt="" />
    </div>
  </div>
</template>

<script>
import SearchList from "@/components/SearchList.vue";
import SearchInput from "@/components/SearchInput.vue";
import SearchCode from "@/components/SearchCode.vue";
import SearchTodo from "@/components/SearchTodo.vue";
import SearchRes from "@/components/SearchRes.vue";

var _ = require("lodash");

export default {
  components: {
    SearchInput,
    SearchList,
    SearchCode,
    SearchTodo,
    SearchRes,
  },
  data() {
    return {
      loading: false,
      CodeList: [],
      history: JSON.parse(localStorage.getItem("history")) || [],
      inputvalue: "",
      searchSuggests: [],
      searchResult: [],
      searchMultimatch: null,
    };
  },
  created() {
    this.loading = true;
    this.axios("https://apis.netstart.cn/music/search/hot").then((res) => {
      this.loading = false;
      this.CodeList = res.data.result.hots;
    });
    this.debouncedGetSearchSuggests = _.debounce(this.getSuggests, 800);
  },
  methods: {
    getSuggests: function (value) {
      this.loading = true;
      this.axios("https://apis.netstart.cn/music/search/suggest", {
        params: {
          keywords: value,
          type: "mobile",
        },
      }).then((res) => {
        this.loading = false;
        this.inputvalue
          ? (this.searchSuggests = res.data.result.allMatch)
          : (this.searchSuggests = []);
      });
    },

    getSearchRes: function (keyword) {
      this.loading = true;
      this.axios("https://apis.netstart.cn/music/search", {
        params: {
          keywords: keyword,
          limit: 10,
          offset: 0,
          type: 1018,
        },
      }).then((res) => {
        this.loading = false;
        this.searchResult = res.data.result.song.songs;
      });
    },

    getMultimatch: function (keyword) {
      this.axios("https://apis.netstart.cn/music/search/multimatch", {
        params: {
          keywords: keyword,
        },
      }).then((res) => {
        this.searchMultimatch = res.data.result;

      });
    },

    addHistory: function (value) {
      this.history = [...new Set([value, ...this.history])];
      this.getSearchRes(value);
      this.getMultimatch(value);
    },

    deleteValue: function () {
      this.inputvalue = "";
      this.searchResult = [];
      this.searchMultimatch = null;
    },

    deleteHistory: function (item) {
      this.history = this.history.filter((o) => {
        return o !== item;
      });
    },

    searchCode: function (value) {
      this.history = [...new Set([value, ...this.history])];
      this.inputvalue = value;
      this.getSearchRes(value);
      this.getMultimatch(value);
    },
    searchHistory: function (value) {
      this.inputvalue = value;
      this.getSearchRes(value);
      this.getMultimatch(value);
    },
    searchSuggest: function (value) {
      this.history = [...new Set([value, ...this.history])];
      this.inputvalue = value;
      this.getSearchRes(value);
      this.getMultimatch(value);
    },
  },

  watch: {
    history: function (newHistory) {
      localStorage.setItem("history", JSON.stringify(newHistory));
    },
    inputvalue: function (n) {
      if (n) {
        this.debouncedGetSearchSuggests(n);
      } else {
        this.searchSuggests = [];
        this.inputvalue = "";
        this.searchResult = [];
        this.searchMultimatch = null;
      }
    },
  },
};
</script>
<style lang="less" scoped>
.search {
  margin-top: 140rem;
  position: relative;

  p {
    width: 100vw;
    font-size: 12rem;
    border-top: 1.5rem solid #ebecec;
    padding-top: 10rem;
    padding-left: 15rem;
  }

  ul.codeList {
    display: flex;
    flex-wrap: wrap;
  }

  ul.searchSuggests {
    p {
      font-size: 15rem;
      color: #507daf;
      padding-left: 15rem;
    }
  }

  .Multimatch {
    .artist {
      width: 100vw;
      display: flex;
      padding: 10rem;
      border-bottom: 1rem solid rgba(0, 0, 0, 0.1);
      .img {
        width: 50px;
        height: 50px;
        margin-right: 15px;
        img {
          display: block;
          width: 100%;
        }
      }
      .content {
        display: flex;
        align-items: center;
        .msg {
          width: calc(100vw - 100rem);
          p {
            width: calc(100vw - 100rem);
            height: 50rem;
            border: none;
            font-size: 17rem;
            line-height: 50rem;
            padding: 0;
            span {
              &:nth-child(1) {
                color: #507daf;
                &.active {
                  color: #333333;
                }
              }
            }
          }
        }
        .icon {
          width: 10rem;
          i {
            color: #9b9b9b;
            font-size: 16rem;
          }
        }
      }
    }

    .album {
      width: 100vw;
      // background: lightblue;

      display: flex;
      padding: 10rem;
      border-bottom: 1rem solid rgba(0, 0, 0, 0.1);
      .img {
        width: 50px;
        height: 50px;
        margin-right: 15px;
        img {
          display: block;
          width: 100%;
        }
      }
      .content {
        display: flex;
        align-items: center;
        .msg {
          width: calc(100vw - 100rem);

          p {
            width: calc(100vw - 100rem);
            border: none;
            font-size: 17rem;
            padding: 0;
            display: flex;
            flex-direction: column;
            span {
              &:nth-child(2) {
                color: #507daf;
                font-size: 12rem;
              }
            }
          }
        }
        .icon {
          width: 10rem;
          i {
            color: #9b9b9b;
            font-size: 16rem;
          }
        }
      }
    }
    .playlist {
      width: 100vw;

      display: flex;
      padding: 10rem;
      border-bottom: 1rem solid rgba(0, 0, 0, 0.1);
      .img {
        width: 50px;
        height: 50px;
        margin-right: 15px;
        img {
          display: block;
          width: 100%;
        }
      }
      .content {
        display: flex;
        align-items: center;
        .msg {
          width: calc(100vw - 100rem);

          p {
            width: calc(100vw - 100rem);
            border: none;
            font-size: 17rem;
            padding: 0;
            display: flex;
            flex-direction: column;
            span {
              &:nth-child(2) {
                color: #a4a4a5;
                font-size: 12rem;
              }
            }
          }
        }
        .icon {
          width: 10rem;
          i {
            color: #9b9b9b;
            font-size: 16rem;
          }
        }
      }
    }
  }

  ul.searchRes {
    width: 100vw;
  }
  div.loading {
    width: 100vw;
    height: calc(100vh - 170rem);
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 170rem;
    left: 0;
    z-index: 9;
    background: white;
    img {
      width: 40rem;
      height: 40rem;
    }
  }
}
</style>

